export async function getBusinesses(api, cid) {
    const entry = await api.query["encointerBazaar"]["businessRegistry"].keys(cid);
    const promises = entry.map(({ args: [cid, controller] }) => api.query["encointerBazaar"]["businessRegistry"](cid, controller).then(business => ({
        controller: controller, business_data: business
    })));
    const businessArray = await Promise.all(promises);
    return api.createType('Vec<Business>', businessArray);
}
